.sideAcontent-container {
    text-align: center;
    padding: 20px;
}

.logo {
    width: 150px; /* Adjust the logo size as needed */
    margin-bottom: 20px;
    border-radius: 15px; 
}
h1 {
    margin-bottom: 10px;
}

.star-rating {
    font-size: 24px;
    color: #FFD700; /* Gold color for the stars */
    margin-bottom: 20px;
}

.description {
    font-size: 18px;
    color: #333;
    margin-bottom: 30px;
}

.image-gallery {
    display: flex;
    justify-content: center;
    gap: 20px; /* Adds space between images */
    overflow-x: auto; /* Allows horizontal scrolling if needed */
}

.image-gallery img {
    width: 150px;
    height: 300px;
    object-fit: cover;
    flex-shrink: 0; /* Prevents the images from shrinking */
    border-radius: 15px; 
}
